import React, { Component } from "react";
import {Card,Elevation, Overlay} from "@blueprintjs/core";
import Typography from '@material-ui/core/Typography';

export default class OverlayAbout extends Component {
    constructor(props) {
      super(props);
        
      this.state = {
        isOpen: false,
      }
    }

    render = () => {
        return (
            <Overlay isOpen={this.state.isOpen} onClose={this.handleClose}>
                <Card className='workspace-overlay-about' interactive={false} elevation={Elevation.ONE}>
                    <Typography color="inherit" variant="h6" align="center">
                        <img src ={require('../../assets/azure_icons/azworkbench-logo.png')} alt="" style={{width : 25, height : 25, marginRight: 3}} />
                        <b>Azure Workbench</b>
                    </Typography>
                    <Typography variant="body1" align="left" gutterBottom>
                        This site is not related to Microsoft or Azure.
                        Hope you enjoy using this tool and it can effectively help you in your daily work.
                    </Typography>
                    <br />
                    <Typography variant="body1" align="left" gutterBottom>
                        Tons of features are lined in the roadmap,
                        next up, deploying diagram to your Azure subscription.
                        Follow by calculate Azure pricing as you drag resource onto canvas...
                    </Typography>
                    <br />
                    <Typography variant="body1" align="left" gutterBottom>
                        Your feedback is upmost important for me to stablize Workbench.
                        Please let me know at: <b>weixianzhang@outlook.com</b>
                    </Typography>
                </Card>
            </Overlay>
        )
    }

    show = () => this.setState({ isOpen: true });

    handleClose = () => this.setState({ isOpen: false });
}